@import "src/variables";

.theme-tab {
  padding-bottom: 2em;

  .preset-switcher {
    margin-right: 1em;
  }

  .btn {
    margin-left: 0.25em;
    margin-right: 0.25em;
  }

  .style-control {
    display: flex;
    align-items: baseline;
    margin-bottom: 5px;

    .label {
      flex: 1;
    }

    .opt {
      margin: 0.5em;
    }

    .color-input {
      flex: 0 0 0;
    }

    input,
    select {
      min-width: 3em;
      margin: 0;
      flex: 0;

      &[type="number"] {
        min-width: 5em;
      }

      &[type="range"] {
        flex: 1;
        min-width: 3em;
        align-self: flex-start;
      }
    }

    &.disabled {
      input,
      select {
        opacity: 0.5;
      }
    }
  }

  .reset-container {
    flex-wrap: wrap;
  }

  .fonts-container,
  .reset-container,
  .apply-container,
  .radius-container,
  .color-container, {
    display: flex;
  }

  .fonts-container,
  .radius-container {
    flex-direction: column;
  }

  .color-container {
    > h4 {
      width: 99%;
    }

    flex-wrap: wrap;
    justify-content: space-between;
  }

  .fonts-container,
  .color-container,
  .shadow-container,
  .radius-container,
  .presets-container {
    margin: 1em 1em 0;
  }

  .tab-header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 100%;
    min-height: 30px;
    margin-bottom: 1em;

    p {
      flex: 1;
      margin: 0;
      margin-right: 0.5em;
    }
  }

  .tab-header-buttons {
    display: flex;
    flex-direction: column;

    .btn {
      min-width: 1px;
      flex: 0 auto;
      padding: 0 1em;
      margin-bottom: 0.5em;
    }
  }

  .shadow-selector {
    .override {
      flex: 1;
      margin-left: 0.5em;
    }

    .select-container {
      margin-top: -4px;
      margin-bottom: -3px;
    }
  }

  .save-load,
  .save-load-options {
    display: flex;
    justify-content: center;
    align-items: baseline;
    flex-wrap: wrap;

    .presets,
    .import-export {
      margin-bottom: 0.5em;
    }

    .import-export {
      display: flex;
    }

    .override {
      margin-left: 0.5em;
    }
  }

  .save-load-options {
    flex-wrap: wrap;
    margin-top: 0.5em;
    justify-content: center;

    .keep-option {
      margin: 0 0.5em 0.5em;
      min-width: 25%;
    }
  }

  .preview-container {
    border-top: 1px dashed;
    border-bottom: 1px dashed;
    border-color: $fallback--border;
    border-color: var(--border, $fallback--border);
    margin: 1em 0;
    padding: 1em;
    background-color: var(--wallpaper);
    background-image: var(--body-background-image);
    background-size: cover;
    background-position: 50% 50%;

    .dummy {
      .post {
        font-family: var(--postFont);
        display: flex;

        .content {
          flex: 1;

          h4 {
            margin-bottom: 0.25em;
          }

          .icons {
            margin-top: 0.5em;
            display: flex;

            i {
              margin-right: 1em;
            }
          }
        }
      }

      .after-post {
        margin-top: 1em;
        display: flex;
        align-items: center;
      }

      .avatar,
      .avatar-alt {
        background:
          linear-gradient(
            135deg,
            #b8e1fc 0%,
            #a9d2f3 10%,
            #90bae4 25%,
            #90bcea 37%,
            #90bff0 50%,
            #6ba8e5 51%,
            #a2daf5 83%,
            #bdf3fd 100%
          );
        color: black;
        font-family: sans-serif;
        text-align: center;
        margin-right: 1em;
      }

      .avatar-alt {
        flex: 0 auto;
        margin-left: 28px;
        font-size: 12px;
        min-width: 20px;
        min-height: 20px;
        line-height: 20px;
        border-radius: $fallback--avatarAltRadius;
        border-radius: var(--avatarAltRadius, $fallback--avatarAltRadius);
      }

      .avatar {
        flex: 0 auto;
        width: 48px;
        height: 48px;
        font-size: 14px;
        line-height: 48px;
      }

      .actions {
        display: flex;
        align-items: baseline;

        .checkbox {
          display: inline-flex;
          align-items: baseline;
          margin-right: 1em;
          flex: 1;
        }
      }

      .separator {
        margin: 1em;
        border-bottom: 1px solid;
        border-color: $fallback--border;
        border-color: var(--border, $fallback--border);
      }

      .btn {
        min-width: 3em;
      }
    }
  }

  .radius-item {
    flex-basis: auto;
  }

  .radius-item,
  .color-item {
    min-width: 20em;
    margin: 5px 6px 0 0;
    display: flex;
    flex-direction: column;
    flex: 1 1 0;

    &.wide {
      min-width: 60%;
    }

    &:not(.wide):nth-child(2n+1) {
      margin-right: 7px;
    }

    .color,
    .opacity {
      display: flex;
      align-items: baseline;
    }
  }

  .theme-radius-rn,
  .theme-color-cl {
    border: 0;
    box-shadow: none;
    background: transparent;
    color: var(--faint, $fallback--faint);
    align-self: stretch;
  }

  .theme-color-cl,
  .theme-radius-in,
  .theme-color-in {
    margin-left: 4px;
  }

  .theme-radius-in {
    min-width: 1em;
    max-width: 7em;
    flex: 1;
  }

  .theme-radius-lb {
    max-width: 50em;
  }

  .theme-preview-content {
    padding: 20px;
  }

  .theme-warning {
    display: flex;
    align-items: baseline;
    margin-bottom: 0.5em;

    .buttons {
      .btn {
        margin-bottom: 0.5em;
      }
    }
  }
}

.extra-content {
  .apply-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-grow: 1;

    /* stylelint-disable-next-line no-descending-specificity */
    .btn {
      flex-grow: 1;
      min-height: 2em;
      min-width: 0;
      max-width: 10em;
      padding: 0;
    }
  }
}
